.youtube-modal {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	justify-content: center;
	align-items: center;
	z-index: 1000;
}

.youtube-modal.open {
	display: flex;
}

.modal-content {
	position: relative;

	padding: 20px;
	border-radius: 5px;
	width: fit-content;
	max-width: 90%;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
}

.close-btn {
	position: absolute;
	top: 10px;
	right: 10px;
	background: none;
	border: none;
	font-size: 1.5rem;
	cursor: pointer;
	color: #fff;
}
